.Services{
    display: flex;
    flex-direction: column;
    gap: 2rem;
    padding: 0 2rem;
}

.services-header{
    display: flex;
    gap: 5rem;
    font-weight: bold;
    font-size: 3rem;
    justify-content: center;
    color: white;
    text-transform: uppercase;
    font-style: italic;
}

.service-categories{
    display: flex;
    gap: 1rem;
}

.category{
    display: flex;
    flex-direction: column;
    background-color: var(--green);
    padding: 2rem;
    gap: 1rem;
    color: white;
    justify-content: space-between;
}

.category>:nth-child(1){
    width: 2rem;
    height: 2rem; fill: white;
}

.category>:nth-child(2){
    font-size: 1rem;
    font-weight: bold;
}

.category>:nth-child(3){
    font-size: 0.9rem;
    line-height: 25px;
}

.contact-now{
    display: flex;
    align-items: center;
    gap: 2rem;
}

.contact-now>img{
    width: 1rem;
}

.category:hover{
    background-color: var(--lghtMaroon);
    cursor: pointer;
}

@media screen and (max-width: 768px){
    .services-header{
        flex-direction: column;
        gap: 1rem;
        font-size: x-large;
        align-items: center;
        justify-content: center;
        margin-top: 5rem;
    }

    .service-categories{
        flex-direction: column;
    }
}